body{
    font-family: 'Noto Sans TC', "Roboto" sans-serif; 
}   
h1,h2,h3,h4,h5,h6,p{
        margin: 0;
        line-height: 1.5;
       }
img {
    vertical-align: middle;
    max-width: 100%;
    height: auto;
}
a,span{
    text-decoration: none;
    display: block;
}
ul,li {
    list-style: none;
    padding: 0;
    margin-bottom: 0;
}

